--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Wrapper and Ratios
--------------------------------------------------------------*/
.media-wrap {
  position: relative;
  overflow: hidden;
}

.media-wrap::before {
  content: '';
  display: block;
  width: 100%;
}

.media-wrap-square::before {
  padding-top: 100%;
}

.media-item {
  display: block;
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/*--------------------------------------------------------------
## WP Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
  border: 0;
  margin-bottom: 0;
  margin-top: 0;
  padding: 0;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
  max-width: 100%;
}

/* Make sure logo link wraps around logo image. */
.custom-logo-link {
  display: inline-block;
}

/*--------------------------------------------------------------
## Captions
--------------------------------------------------------------*/
.wp-caption {
  margin-bottom: 1.5em;
  max-width: 100%;
}

.wp-caption .wp-caption-text {
  margin: 0.8075em 0;
}

.wp-caption-text {
  text-align: center;
}

/*--------------------------------------------------------------
## Galleries
--------------------------------------------------------------*/
.gallery {
  margin-bottom: 1.5em;
}

.gallery-item {
  display: inline-block;
  text-align: center;
  vertical-align: top;
  width: 100%;
}

.gallery-columns-2 .gallery-item {
  max-width: 50%;
}

.gallery-columns-3 .gallery-item {
  max-width: 33.33333%;
}

.gallery-columns-4 .gallery-item {
  max-width: 25%;
}

.gallery-columns-5 .gallery-item {
  max-width: 20%;
}

.gallery-columns-6 .gallery-item {
  max-width: 16.66667%;
}

.gallery-columns-7 .gallery-item {
  max-width: 14.28571%;
}

.gallery-columns-8 .gallery-item {
  max-width: 12.5%;
}

.gallery-columns-9 .gallery-item {
  max-width: 11.11111%;
}

.gallery-caption {
  display: block;
}

img {
  max-width: 100%;
  display: block;
  height: initial;
}

/*--------------------------------------------------------------
# Components / Templates
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Header
--------------------------------------------------------------*/
.site-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 15px;
  background-color: #000;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 60px;
  z-index: 10;
}

.site-branding a {
  color: #fff;
}

/*--------------------------------------------------------------
## Menu Btn
--------------------------------------------------------------*/
.menu-btn {
  display: none;
  width: 30px;
  height: 19px;
  right: 15px;
  top: 21px;
  position: absolute;
  z-index: 110;
}

.menu-btn::after, .menu-btn::before,
.menu-btn span {
  width: 100%;
  height: 3px;
  position: absolute;
  display: block;
  background-color: #fff;
}

.menu-btn::after, .menu-btn::before {
  content: '';
  transition: 350ms width 350ms ease;
}

.menu-btn::after {
  top: 0;
  left: 0;
}

.menu-btn::before {
  bottom: 0;
  right: 0;
}

.menu-btn span {
  top: 50%;
  margin-top: -1.5px;
  transition: transform 350ms ease;
}

.menu-btn span::before {
  content: '';
  background-color: #fff;
  width: 100%;
  height: 3px;
  position: absolute;
  left: 0;
  transition: transform 350ms ease;
}

.menu-btn.open::after, .menu-btn.open::before {
  transition: 350ms ease;
  width: 0;
}

.menu-btn.open::after {
  left: 0;
}

.menu-btn.open::before {
  right: 0;
}

.menu-btn.open span {
  transform: rotate(45deg);
  transition: 350ms transform 350ms ease;
  position: relative;
}

.menu-btn.open span::before {
  transform: rotate(-90deg);
  transition: 350ms transform 350ms ease;
}

@media screen and (max-width: 640px) {
  .menu-btn {
    display: block;
  }
}

/*--------------------------------------------------------------
## Navigation
--------------------------------------------------------------*/
a {
  text-decoration: none;
}

a:hover, a:focus, a:active {
  text-decoration: none;
  outline: 0;
}

.main-navigation .primary-menu {
  list-style: none;
  margin: 0;
  padding: 0;
}

.main-navigation .primary-menu li:hover > ul,
.main-navigation .primary-menu li.focus > ul {
  transform: translateY(0);
  display: block;
  opacity: 1;
}

.main-navigation .sub-menu {
  position: absolute;
  left: 0;
  top: 100%;
  border-top: 3px solid #fff;
  z-index: 99999;
  background-color: #000;
  transition: 350ms ease;
  transform: translateY(5px);
  display: none;
  opacity: 0;
}

.main-navigation .sub-menu .sub-menu {
  left: 100%;
  top: 0;
}

.main-navigation .sub-menu li {
  border-bottom: 1px solid #000;
}

.main-navigation .sub-menu li:last-of-type {
  border-bottom: none;
}

.main-navigation .sub-menu a {
  width: 200px;
}

.main-navigation li {
  position: relative;
  float: left;
  transition: 350ms ease;
}

.main-navigation li:hover > a,
.main-navigation li.focus > a {
  background-color: #fff;
}

.main-navigation a {
  display: block;
  text-decoration: none;
  color: #fff;
  padding: 20px 15px;
}

@media screen and (max-width: 640px) {
  .main-navigation {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    visibility: hidden;
    z-index: 100;
    transition: 350ms ease 350ms;
  }
  .main-navigation::before {
    content: '';
    display: block;
    background-color: rgba(0, 0, 0, 0.4);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: 350ms ease;
  }
  .main-navigation.open {
    visibility: visible;
    transition: none;
  }
  .main-navigation.open::before {
    opacity: 1;
    transition: 350ms ease 10ms;
  }
  .main-navigation.open > ul {
    transform: translateX(0);
    transition: 350ms ease 10ms;
  }
  .main-navigation .primary-menu {
    position: absolute;
    top: 0;
    right: 0;
    width: 230px;
    height: 100%;
    background-color: #000;
    padding-top: 60px;
    overflow: auto;
    z-index: 100;
    transform: translateX(100%);
    transition: 350ms ease;
  }
  .main-navigation .sub-menu {
    position: static;
    transition: initial;
    transform: none;
    background-color: transparent;
    border: 0;
    padding: 5px 0 10px 10px;
    opacity: 1;
    display: none;
  }
  .main-navigation .sub-menu li {
    border: 0;
    font-size: .85em;
  }
  .main-navigation .sub-menu .sub-icon {
    top: 1px;
  }
  .main-navigation li {
    float: none;
  }
  .main-navigation li:hover > a,
  .main-navigation li.focus > a {
    background-color: transparent;
  }
  .main-navigation a {
    display: inline-block;
    padding: 0 15px;
  }
  .main-navigation .sub-icon {
    line-height: 1;
    display: block;
    color: #fff;
    font-size: 18px;
    position: absolute;
    right: 60px;
    top: 3px;
    transition: 350ms ease;
  }
}

.site-main .comment-navigation, .site-main
.posts-navigation, .site-main
.post-navigation {
  margin: 0 0 1.5em;
  overflow: hidden;
}

.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
  float: left;
  width: 50%;
}

.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
  float: right;
  text-align: right;
  width: 50%;
}

