/*--------------------------------------------------------------
## Entry Content
--------------------------------------------------------------*/
.entry-content {
  font-size: 16px;
  margin: 5px 0 0;
}

.entry-content h1,
.entry-content h2,
.entry-content h3,
.entry-content h4,
.entry-content h5,
.entry-content h6 {
  line-height: 1.35;
  margin-bottom: 15px;
  color: #000;
}

.entry-content h2 {
  font-size: 24px;
  font-weight: 700;
}

.entry-content h3 {
  font-size: 18px;
}

.entry-content h4 {
  font-size: 14px;
}

.entry-content h5 {
  font-size: 12px;
}

.entry-content h6 {
  font-size: 10px;
}

.entry-content p {
  line-height: 1.45;
  margin-bottom: 27px;
}

.entry-content p strong {
  font-weight: 400;
  color: #000;
}

.entry-content li {
  margin-bottom: 4px;
}

.entry-content li:last-child {
  margin-bottom: 0;
}

.entry-content ul,
.entry-content ol {
  margin-bottom: 27px;
}

.entry-content ul {
  margin-left: 20px;
}

.entry-content ul li::before {
  content: '-';
  display: inline-block;
  margin-right: 4px;
}

.entry-content ol {
  margin-left: 28px;
}

.entry-content ol li {
  list-style: decimal;
}

.entry-content a {
  display: inline-block;
  position: relative;
}

.entry-content a::before {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  height: 1px;
  width: 0;
  background: #000;
  transition: 350ms ease;
}

.entry-content a:hover::before {
  width: 100%;
}

.entry-content table {
  border-collapse: collapse;
  color: #000;
  margin: 25px 0 35px;
  overflow-x: visible;
  table-layout: fixed;
  width: 100%;
  max-width: 700px;
  display: table;
}

@media screen and (max-width: 767pxpx) {
  .entry-content table {
    display: block;
    overflow-x: scroll;
  }
}

.entry-content tbody {
  width: 100%;
}

@media screen and (max-width: 767pxpx) {
  .entry-content tbody {
    width: 700px;
    display: block;
  }
}

.entry-content tr {
  vertical-align: top;
}

.entry-content tr:first-child td {
  padding-top: 0;
}

.entry-content th {
  border-bottom: 2px solid #000;
  font-weight: 400;
  padding-bottom: 8px;
  color: #000;
}

.entry-content td {
  border-bottom: 1px solid #000;
  padding: 20px 0 5px;
}

